import React,{useEffect, useState} from 'react'
import {useNavigate} from 'react-router-dom'
import {useSpeechSynthesis} from 'react-speech-kit';
import moment from 'moment';

export default function FinishScreen() {

  const {speak, voices, cancel} = useSpeechSynthesis();
  const [speaking, setSpeaking] = useState(false)
  const [tap, setTap] = useState(0)
  const [totalTime, setTotalTime] = useState(0)

  useEffect(()=>{
    if(speaking==true && localStorage.getItem("Playpromptaudio")=='true')
    {
        speak({ text: localStorage.getItem("welldone"),voice:localStorage.getItem("Promptvoice")=="male" ? voices[parseInt(localStorage.getItem('male'))] : voices[parseInt(localStorage.getItem('female'))], rate:localStorage.getItem("Speechrate")=='normal' ? 1 : localStorage.getItem("Speechrate")=='slow' ? 0.65 : 1.15})
    }
  },[speaking])

  useEffect(()=>{
    const dt= new Date();
    const dtFinal = (moment(dt).valueOf() - moment(localStorage.getItem('totaltime')).valueOf())/1000
    console.log(dtFinal)
    console.log(localStorage.getItem('totaltap'))
    setTap(localStorage.getItem('totaltap'))
    setTotalTime(dtFinal)

    
    const firstTime =setTimeout(() => {
      console.log("slplpl")
      setSpeaking(true)
    }, 1000);
    return ()=> clearTimeout(firstTime)
  },[])
  const navigate = useNavigate()
  return (
    <div style={{backgroundColor:'#fff', height:'100vh'}}>
      {/* Well done. The task is finish */}
        <div className='start-heading-finish'>{localStorage.getItem("welldone")}</div>
        <button onClick={()=>{navigate('/');cancel()}} className='start-task-btn'>Restart Task</button>
        <div className='gap'></div>
        <p className='textp'>{tap} taps &nbsp; &nbsp;  ~{parseInt(totalTime)} total seconds</p>
        <p className='textp'></p>
        {/* <p className='textp'>Seconds per tap ~{parseInt(totalTime)}</p> */}
        {/* <button className='setting-btn mt-costom'>Change Setting</button>
        <button className='setting-btn'>Quit App</button> */}
    </div>
  )
}
