import './App.css';
import React,{useEffect} from 'react'
import Settings from './Screens/Settings';
import SettingPage from './Screens/SettingPage';
import Display from './Screens/Display';
import StartScreen from './Screens/StartScreen';
import FinishScreen from './Screens/FinishScreen';
import TimeOut from './Screens/TimeOut';
import WrongOption from './Screens/WrongOption';
import HomeScreen from './Screens/HomeScreen';
import TextSize from './Screens/TextSize';
import {
  Routes,
  Route,
  useLocation,
  json
} from "react-router-dom";
// import 'antd/dist/antd.css';
import {AnimatePresence } from "framer-motion"
import SpeakPrompts from './Screens/SpeakPrompts';

function App() {
  const location= useLocation()

  useEffect(()=>{
    const data = localStorage.getItem("data")
    console.log(data)
    if(data==null)
    {
      localStorage.setItem("Secondsuntilprompt", 3.0)
      localStorage.setItem("Secondsuntilfirstprompt", 3.0)
      localStorage.setItem("Secondsuntilreprompt",3.0)
      localStorage.setItem("Numberofretries",3.0)
      localStorage.setItem("Showpromptarrow",true);
      localStorage.setItem("Retriesuntilarrow",0.0)
      localStorage.setItem("Secondsofshow",3.0)
      localStorage.setItem("Usearrowanimation",true)
      localStorage.setItem("Arrowanimation",'poke');
      localStorage.setItem("Usepagetransitions",true)
      localStorage.setItem("Playpromptaudio",true)
      localStorage.setItem("Retriesuntilspeech",0.0)
      localStorage.setItem("Promptvoice","male")
      localStorage.setItem("Speechrate",'normal')
      localStorage.setItem("usehapticfeedback",false)
      localStorage.setItem("Retriesuntilvibrate",3.0)
      localStorage.setItem("data",true)
      localStorage.setItem("time",0)
      localStorage.setItem("textOnPhoneBigger","Make the text on your phone bigger")
      localStorage.setItem("tapSettingsBtn","Tap the settings Button")
      localStorage.setItem("swipeUpDisplayBrightness","Swipe up to Display and Brightness")
      localStorage.setItem("tapDisplayBrightness","Tap Display and Brightness")
      localStorage.setItem("swipedownTextSize","Swipe up to Text Size")
      localStorage.setItem("tapTextSize","Tap Text Size")
      localStorage.setItem("tapTheBarRight","Tap the bar to the right")
      localStorage.setItem("taskTimeOut","Task time out")
      localStorage.setItem("welldone","Well done! The task is finished")
      localStorage.setItem("tapback","Tap back")
    }
    
    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    // Windows Phone must come first because its UA also contains "Android"
    if (/windows phone/i.test(userAgent)) {
     localStorage.setItem("OS","Windows Phone");
     console.log("Windows Phone")
    }

    if (/android/i.test(userAgent)) {
      localStorage.setItem("OS","Android");
    }

    // iOS detection from: http://stackoverflow.com/a/9039885/177710
    if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
      localStorage.setItem("OS","IOS");
    }
  },[])
  return (
    
      <div className='app'>
        <meta name="apple-mobile-web-app-capable" content="yes" />
        <AnimatePresence>
        <Routes key={location.pathname} location={location}>
          <Route exact path="/home-screen" element={<HomeScreen />} />
          <Route exact path="/time-out" element={<TimeOut />} />
          <Route exact path="/finish-screen" element={<FinishScreen />} />
          <Route exact path="/" element={<StartScreen />} />
          <Route exact path="/display" element={<Display />} />
          <Route exact path="/setting-page" element={<SettingPage />} />
          <Route exact path="/settings" element={<Settings />} />
          <Route exact path="/wrong-option" element={<WrongOption />} />
          <Route exact path="/text-size" element={<TextSize />} />
          <Route exact path="/speak-prompts" element={<SpeakPrompts />} />
        </Routes>
        </AnimatePresence>
      </div>
  );
}

export default App;
