import React from 'react'
import {RightOutlined} from '@ant-design/icons';
import {useNavigate, Link} from "react-router-dom";

export default function General(props) {
  const navigate = useNavigate()

  return (
    <div className='SettingPageContent' onClick={()=>{props.cancel();localStorage.setItem("totaltap", parseFloat(localStorage.getItem('totaltap'))+1);window.scrollTo(0, 0);navigate('/wrong-option',{state:{heading:props.title,route:'/setting-page'}})}}>
        <img src={props.img} className='SettingPageContentImg' />
        <div className='SettingPageContentText'>{props.title}</div>
        <RightOutlined className='SettingPageContentIcon' /> 
    </div>
  )
}
