import React,{useEffect, useState} from 'react'
import {useNavigate} from 'react-router-dom'
import {useSpeechSynthesis} from 'react-speech-kit';

export default function TimeOut() {
  const {speak, voices,cancel} = useSpeechSynthesis();
  const [speaking, setSpeaking] = useState(false)

  useEffect(()=>{
    if(speaking==true && localStorage.getItem("Playpromptaudio")=='true')
    {
        speak({ text: localStorage.getItem("taskTimeOut"),voice:localStorage.getItem("Promptvoice")=="male" ? voices[parseInt(localStorage.getItem('male'))] : voices[parseInt(localStorage.getItem('female'))], rate:localStorage.getItem("Speechrate")=='normal' ? 1 : localStorage.getItem("Speechrate")=='slow' ? 0.65 : 1.15})
    }
  },[speaking])

  useEffect(()=>{
    const firstTime = setTimeout(() => {
      console.log("slplpl")
      setSpeaking(true)
    }, 1000);
    return ()=> clearTimeout(firstTime)
  },[])
  const navigate = useNavigate()
  return (
    <div style={{backgroundColor:'#fff', height:'100%'}}>
      {/* Task timed out. */}
    <div className='start-heading'>{localStorage.getItem("taskTimeOut")}</div>
    <button onClick={()=>{cancel();navigate('/', {replace: true})}} className='start-task-btn'>Restart Task</button>
    {/* <button className='setting-btn mt-costom'>Change Setting</button>
    <button className='setting-btn'>Quit App</button> */}
</div>
  )
}
