import React from 'react'
import {useNavigate} from "react-router-dom";
import {LeftOutlined} from '@ant-design/icons';

export default function Header(props) {
    const navigate = useNavigate();

    

  return (
    <div className='Header'>
        {props.isBack===true && <div className='back-button' onClick={()=>{navigate(props.route,{state:{returnVal:true},replace:true});localStorage.setItem("totaltap", parseFloat(localStorage.getItem('totaltap'))+1);props.cancel()}}><LeftOutlined className='back-button-icon' />  Back</div>}
        <div className='heading'>{props.heading}</div>
        {props.isReset===true ? <div className='reset-button' onClick={()=>props.ResetFun()}>Reset</div>
        : <div className='reset-button1'>Reset</div>}
    </div>
  )
}
