import React from 'react'
import {RightOutlined} from '@ant-design/icons';
import {useNavigate, Link} from "react-router-dom";
import { motion as m } from "framer-motion";

export default function DisplayBtn(props) {
  const navigate = useNavigate()

  return (
    <m.div
    initial={{ x:'100%' }}
    animate={{ x:'0%' }}
    exit={{x:'-100%',transition:{duration:0.1}}}
    transition={{ duration: 0.3}}
     className='SettingPageContent' onClick={()=>{window.scrollTo(0, 0);localStorage.setItem("totaltap", parseFloat(localStorage.getItem('totaltap'))+1);navigate('/display',{state:{heading:props.title}});props.cancel()}}>
        <img src={props.img} className='SettingPageContentImg' />
        <div className='SettingPageContentText'>{props.title}</div>
        <RightOutlined className='SettingPageContentIcon' /> 
    </m.div>
  )
}
