import React, {useEffect, useState} from 'react'
import SpeakSinglePrompt from '../Components/SpeakSinglePrompt';
import Header from '../Components/Header';

export default function SpeakPrompts() {

    const [textOnPhoneBigger, settextOnPhoneBigger] = useState('')
    const [tapSettingsBtn,settapSettingsBtn] = useState('')
    const [swipeUpDisplayBrightness,setswipeUpDisplayBrightness] = useState('')
    const [tapDisplayBrightness,settapDisplayBrightness] = useState('')
    const [swipedownTextSize,setswipeDownTextSize] = useState('')
    const [tapTextSize,setTapTextSize] = useState('')
    const [tapTheBarRight,setTapTheBarRight] = useState('')
    const [taskTimeOut, settaskTimeOut] = useState('')
    const [welldone,setwelldone] = useState('')
    const [tapBack,setTapBack] = useState('')

    useEffect(()=>{
        settextOnPhoneBigger(localStorage.getItem("textOnPhoneBigger"));
        settapSettingsBtn(localStorage.getItem("tapSettingsBtn"))
        setswipeUpDisplayBrightness(localStorage.getItem("swipeUpDisplayBrightness"))
        settapDisplayBrightness(localStorage.getItem("tapDisplayBrightness"))
        setswipeDownTextSize(localStorage.getItem("swipedownTextSize"))
        setTapTextSize(localStorage.getItem("tapTextSize"))
        setTapTheBarRight(localStorage.getItem("tapTheBarRight"))
        settaskTimeOut(localStorage.getItem("taskTimeOut"))
        setwelldone(localStorage.getItem("welldone"))
        setTapBack(localStorage.getItem("tapback"))
    },[])

    const ResetFun =() =>{
      localStorage.setItem("textOnPhoneBigger","Make the text on your phone bigger")
      localStorage.setItem("tapSettingsBtn","Tap the settings Button")
      localStorage.setItem("swipeUpDisplayBrightness","Swipe up to Display and Brightness")
      localStorage.setItem("tapDisplayBrightness","Tap Display and Brightness")
      localStorage.setItem("swipedownTextSize","Swipe up to Text Size")
      localStorage.setItem("tapTextSize","Tap Text Size")
      localStorage.setItem("tapTheBarRight","Tap the bar to the right")
      localStorage.setItem("taskTimeOut","Task time out")
      localStorage.setItem("welldone","Well done! The task is finished")
      localStorage.setItem("tapback","Tap back")
      window.location.reload()
    }

  return (
    <div>
        <Header heading="Step Instructions" isBack={true} isReset={true} route='/settings' ResetFun={ResetFun} />
        <div className='speak-container'>
        <SpeakSinglePrompt num={1} propmtVal={textOnPhoneBigger} setPromptVal={settextOnPhoneBigger} />
        <SpeakSinglePrompt num={2} propmtVal={tapSettingsBtn} setPromptVal={settapSettingsBtn} />
        <SpeakSinglePrompt num={3} propmtVal={swipeUpDisplayBrightness} setPromptVal={setswipeUpDisplayBrightness} />
        <SpeakSinglePrompt num={4} propmtVal={tapDisplayBrightness} setPromptVal={settapDisplayBrightness} />
        <SpeakSinglePrompt num={5} propmtVal={swipedownTextSize} setPromptVal={setswipeDownTextSize} />
        <SpeakSinglePrompt num={6} propmtVal={tapTextSize} setPromptVal={setTapTextSize} />
        <SpeakSinglePrompt num={7} propmtVal={tapTheBarRight} setPromptVal={setTapTheBarRight} />
        <SpeakSinglePrompt num={8} propmtVal={taskTimeOut} setPromptVal={settaskTimeOut} />
        <SpeakSinglePrompt num={9} propmtVal={welldone} setPromptVal={setwelldone} />
        <SpeakSinglePrompt num={10} propmtVal={tapBack} setPromptVal={setTapBack} />
        </div>
    </div>
  )
}
