import React,{useEffect, useState} from 'react'
import Header from '../Components/Header'
import Heading from '../Components/Heading'
import PromptContainer from '../Components/PromptContainer'
import { motion as m } from "framer-motion";
import {RightOutlined} from '@ant-design/icons';
import {useNavigate} from "react-router-dom";

export default function Settings() {

            //Increment Values
  const [promptVal, setPromptVal] = useState(3)
  const [repromptVal, setRepromptVal] = useState(3)
  const [retries, setRetries] = useState(3)
  const [retriesArrow, setRetriesArrow] = useState(3)
  const [secondsShow, setSecondsShow] = useState(3)
  const [retriesSeconds, setRetriesSeconds] = useState(3)
  const [retriesVibrate, setRetriesVibrate] = useState(3)
  const [secondsUntilFirstPrompt, setsecondsUntilFirstPrompt] = useState(3);

  //Switches
  const [showArrow, setShowArrow] = useState(localStorage.getItem("Showpromptarrow"));
  const [ArrowAnimation, setArrowAnimation] = useState(localStorage.getItem("Usearrowanimation"));
  const [pageTransition, setPageTransition] = useState(localStorage.getItem("Usepagetransitions"));
  const [promptAudio, setPromptAudio] = useState(localStorage.getItem("Playpromptaudio"));
  const [hapticFeedback, setHapticFeedback] = useState(localStorage.getItem("usehapticfeedback"));
  
  const [loading, setLoading] = useState(false)

  //Radio
  const [arrowAnim,setArrowAnim] = useState('fade')
  const [voice, setVoice] = useState('male')
  const [speech, setSpeech] = useState('normal')

  const navigate = useNavigate();
  


  const data = [
    {
      label: 'Fade',
      value: 'fade',
    },
    {
      label: 'Poke',
      value: 'poke',
    },
    {
      label: 'Blink',
      value: 'blink',
    },
  ];

  const voiceData = [
    {
      label: 'Male',
      value: 'male',
    },
    {
      label: 'Female',
      value: 'female',
    }
  ];

  const speechData = [
    {
      label: 'Slow',
      value: 'slow',
    },
    {
      label: 'Normal',
      value: 'normal',
    },
    {
      label: 'Fast',
      value: 'fast',
    }
  ];
 

  useEffect(()=>{
    setShowArrow(localStorage.getItem("Showpromptarrow"))
    setPromptVal(localStorage.getItem("Secondsuntilprompt"))
    setRepromptVal(localStorage.getItem("Secondsuntilreprompt"))
    setRetries(localStorage.getItem("Numberofretries"))
    

    setRetriesArrow(localStorage.getItem("Retriesuntilarrow"))
    setsecondsUntilFirstPrompt(localStorage.getItem("Secondsuntilfirstprompt"))
    setSecondsShow(localStorage.getItem("Secondsofshow"))

    setArrowAnimation(localStorage.getItem("Usearrowanimation"))

    setArrowAnim(localStorage.getItem("Arrowanimation"))

    setPageTransition(localStorage.getItem("Usepagetransitions"))
    setPromptAudio(localStorage.getItem("Playpromptaudio"))

    setRetriesSeconds(localStorage.getItem("Retriesuntilspeech"))
    setVoice(localStorage.getItem("Promptvoice"))
    setSpeech(localStorage.getItem("Speechrate"))
    setHapticFeedback(localStorage.getItem("usehapticfeedback"))
    setRetriesVibrate(localStorage.getItem("Retriesuntilvibrate"))
  },[])

  const ResetFun =() =>{
    localStorage.setItem("Secondsuntilprompt", 3.0)
    localStorage.setItem("Secondsuntilfirstprompt", 3.0)
    localStorage.setItem("Secondsuntilreprompt",3.0)
    localStorage.setItem("Numberofretries",3.0)
    localStorage.setItem("Showpromptarrow",true);
    localStorage.setItem("Retriesuntilarrow",0.0)
    localStorage.setItem("Secondsofshow",3.0)
    localStorage.setItem("Usearrowanimation",true)
    localStorage.setItem("Arrowanimation",'poke');
    localStorage.setItem("Usepagetransitions",true)
    localStorage.setItem("Playpromptaudio",true)
    localStorage.setItem("Retriesuntilspeech",0.0)
    localStorage.setItem("Promptvoice","male")
    localStorage.setItem("Speechrate",'normal')
    localStorage.setItem("usehapticfeedback",false)
    localStorage.setItem("Retriesuntilvibrate",3.0)
    localStorage.setItem("data",true)
    

    setShowArrow(true)
    setPromptVal(localStorage.getItem("Secondsuntilprompt"))
    setsecondsUntilFirstPrompt(localStorage.getItem("Secondsuntilfirstprompt"))
    setRepromptVal(localStorage.getItem("Secondsuntilreprompt"))
    setRetries(localStorage.getItem("Numberofretries"))
    

    setRetriesArrow(localStorage.getItem("Retriesuntilarrow"))
    setSecondsShow(localStorage.getItem("Secondsofshow"))

    setArrowAnimation(true)

    setArrowAnim(localStorage.getItem("Arrowanimation"))

    setPageTransition(true)
    setPromptAudio(false)

    setRetriesSeconds(localStorage.getItem("Retriesuntilspeech"))
    setVoice(localStorage.getItem("Promptvoice"))
    setSpeech(localStorage.getItem("Speechrate"))
    setHapticFeedback(false)
    setRetriesVibrate(localStorage.getItem("Retriesuntilvibrate"))
    window.location.reload()
  }

  const cancel = () =>{

  }
  
  return (
    <m.div
    initial={{ x:localStorage.getItem("Usepagetransitions")=='true' ? '100%' : '0%' }}
    animate={{ x:'0%' }}
    exit={{x:localStorage.getItem("Usepagetransitions")=='true' ? '-100%':'0%',transition:{duration:0.1}}}
    transition={{ duration: 0.3, ease:'easeIn'}}
    > 
        <Header heading="Settings" isBack={true} isReset={true} cancel={cancel} route='/' ResetFun={ResetFun} />
        
        <Heading heading="TIMING" />

        <PromptContainer num={1} heading="Seconds until prompt" incrementValue={promptVal} setPromptVal ={setPromptVal} promptVal={promptVal} incrementPrompt={true} />
        <PromptContainer num={16} heading="Seconds until first prompt" incrementValue={secondsUntilFirstPrompt} setPromptVal ={setsecondsUntilFirstPrompt} promptVal={secondsUntilFirstPrompt} incrementPrompt={true} />
        <PromptContainer num={2} heading="Seconds until reprompt" incrementValue={repromptVal} setPromptVal ={setRepromptVal} promptVal={repromptVal} incrementPrompt={true} />
        <PromptContainer num={3} heading="Number of retries" incrementValue={retries} setPromptVal ={setRetries} promptVal={retries} incrementPrompt={true} />
        
        <Heading heading="VISUALS" />
        <PromptContainer num={4} heading="Show prompt arrow" switch={true} default={showArrow} onChange={setShowArrow} />
        <PromptContainer num={5} heading="Retries until arrow"  incrementValue={retriesArrow} setPromptVal ={setRetriesArrow} promptVal={retriesArrow} incrementPrompt={true}/>
        <PromptContainer num={6} heading="Seconds of show"  incrementValue={secondsShow} setPromptVal ={setSecondsShow} promptVal={secondsShow} incrementPrompt={true}/>
        <PromptContainer num={7} heading="Use arrow animation" switch={true} default={ArrowAnimation} onChange={setArrowAnimation} />
        <PromptContainer num={8} heading="Arrow animation" value={arrowAnim} setValue={setArrowAnim} data={data} radio={true} />
        <PromptContainer num={9} heading="Use page transitions" switch={true} default={pageTransition} onChange={setPageTransition} />

        <Heading heading="AUDIO" />
        <PromptContainer num={10} heading="Play prompt audio" switch={true} default={promptAudio} onChange={setPromptAudio} />
        <PromptContainer num={11} heading="Retries until speech" incrementValue={retriesSeconds} setPromptVal ={setRetriesSeconds} promptVal={retriesSeconds} incrementPrompt={true} />
        <PromptContainer num={12} heading="Prompt voice" data={voiceData} value={voice} setValue={setVoice} radio={true} />
        <PromptContainer num={13} heading="Speech rate" data={speechData} value={speech} setValue={setSpeech} radio={true} />
        <div onClick={()=>navigate('/speak-prompts')} className='prompt-Container'>
          <div className='prompt-Container-heading'>Edit step instruction</div>
          <div className='arrow-container1'>
            <div className='arrow-text'><RightOutlined/></div> 
          </div>
        </div>

        <Heading heading="HAPTIC" />
        <PromptContainer num={14} heading="use haptic feedback" switch={true} default={hapticFeedback} onChange={setHapticFeedback} />
        <PromptContainer num={15} heading="Retries until vibrate" incrementValue={retriesVibrate} setPromptVal ={setRetriesVibrate} promptVal={retriesVibrate} incrementPrompt={true} />

    </m.div>
  )
}
