import React,{useState, useEffect} from 'react'
import { Switch } from 'antd';
import {RightOutlined} from '@ant-design/icons';
import {useNavigate} from "react-router-dom";

export default function DisplayGeneral(props) {

    const [checked, setChecked]= useState(props.default)
    const navigate = useNavigate();

    const onChange = (checked) => {
        console.log(`switch to ${checked}`);
        setChecked(checked)
      };

      useEffect(()=>{
        console.log("props.navigate" + props.navigate)
      },[])

  return (
    <div onClick={()=>{props.navigate!==undefined ? navigate(props.navigate) : console.log("Nulll");props.navigate!==undefined && props.cancel(); props.navigate!==undefined && localStorage.setItem("totaltap", parseFloat(localStorage.getItem('totaltap'))+1);}} className='display-sub-container' style={{borderColor:props.border==true ?'#cfcfd1': '#fff', marginBottom:props.border==true ? '3vw': '0vw'}} >

      <div className='display-Container-heading'>{props.heading}</div>

      {props.switch===true && <div className='increment-container' style={{paddingRight:'5%'}}>
        <Switch defaultChecked={props.default} style={{ backgroundColor: checked ? '#4cd964' : '#efeff4'}} onChange={onChange} />
      </div>
      }

      {props.arrow===true && <div className='arrow-container1'>
        <div className='arrow-text'>{props.subHeading} <RightOutlined/></div> 
      </div>
      }
    </div>
  )
}
