import React,{useEffect, useState} from 'react'
import { motion as m } from "framer-motion";
import {useNavigate, Link} from "react-router-dom";
import {useSpeechSynthesis} from 'react-speech-kit';

export default function StartScreen() {
  const navigate = useNavigate()
  const [speaking, setSpeaking] = useState(false)

  const [operatingSystem, setoperatingSystem] = useState('')

  const {speak, voices, cancel} = useSpeechSynthesis();

  useEffect(()=>{
    if(speaking==true && localStorage.getItem("Playpromptaudio")=='true')
    {
      console.log(voices)
      voices.map((item,index)=>{
        if(item.name=='Samantha')
        {
          console.log("Female voice")
          console.log(item)
          localStorage.setItem('female',index)
        }
        else if(item.name=='Daniel')
        {
          console.log("Male voice")
         console.log(item)
         localStorage.setItem('male',index)
        }
      })
      //Samantha 108 Daniel 0
      speak({ text: localStorage.getItem("textOnPhoneBigger"),voice:localStorage.getItem("Promptvoice")=="male" ? voices[parseInt(localStorage.getItem('male'))] : voices[parseInt(localStorage.getItem('female'))], rate:localStorage.getItem("Speechrate")=='normal' ? 1 : localStorage.getItem("Speechrate")=='slow' ? 0.65 : 1.15})
   //   localStorage.setItem("OS")=="Android" && navigator.vibrate(300)
    } 
  },[speaking])

  useEffect(()=>{
    localStorage.setItem("totaltime",0)
    localStorage.setItem("totaltap",0)
    const firstTime = setTimeout(() => {
     
      setSpeaking(true)
    }, 2000);
    return ()=> clearTimeout(firstTime)
   
    
  },[])

  return (
    <m.div 
    initial={{ x:localStorage.getItem("Usepagetransitions")=='true' ? '100%' : '0%' }}
    animate={{ x:'0%' }}
    exit={{x:localStorage.getItem("Usepagetransitions")=='true' ? '-100%':'0%',transition:{duration:0.1}}}
    transition={{ duration: 0.3, ease:'easeIn'}}
    style={{backgroundColor:'#fff', height:'100vh'}}>
        <div className='start-heading'>{localStorage.getItem("textOnPhoneBigger")}</div>
        <button onClick={()=>{cancel();localStorage.setItem("totaltap", parseFloat(localStorage.getItem('totaltap'))+1); navigate('/home-screen', {replace: true});  speak({ text: '',voice:localStorage.getItem("Promptvoice")=="male" ? voices[''] : voices[localStorage.getItem("OS")=='IOS' ? 50 : localStorage.getItem("OS")=='Android' ? 66 : 50], rate:localStorage.getItem("Speechrate")=='normal' ? 1 : localStorage.getItem("Speechrate")=='slow' ? 0.85 : 1.05})}} className='start-task-btn'>Start Task</button>
        <button onClick={()=>{cancel(); navigate('/settings');  speak({ text: '',voice:localStorage.getItem("Promptvoice")=="male" ? voices[''] : voices[localStorage.getItem("OS")=='IOS' ? 50 : localStorage.getItem("OS")=='Android' ? 66 : 50], rate:localStorage.getItem("Speechrate")=='normal' ? 1 : localStorage.getItem("Speechrate")=='slow' ? 0.85 : 1.05})}} className='setting-btn mt-costom'>Change Setting</button>
        <button className='setting-btn'>Quit App</button>
    </m.div>
  )
}
