import React,{useState, useEffect} from 'react'
import { Switch, Radio } from 'antd';

export default function PromptContainer(props) {

  const [checked, setChecked]= useState(props.default=='true' ? true : false)

  useEffect(()=>{
  if(props.num==4)
  {
    console.log("Checked value is " + props.default + "  " + checked)
    console.log(typeof(checked))
  }
  
  },[])

  const promptIncrement =()=>{
    if(props.promptVal<21)
    {
      const val = parseFloat(props.promptVal)+1;
      if(props.num==1)
      {
        localStorage.setItem("Secondsuntilprompt",val)
        
      } 
      else if(props.num==2)
      {
        localStorage.setItem("Secondsuntilreprompt",val)
      } 
      else if(props.num==3)
      {
       localStorage.setItem("Numberofretries",val) 
      } 
      else if(props.num==5)
      {
        localStorage.setItem("Retriesuntilarrow",val)
      } 
      else if(props.num==6)
      {
        localStorage.setItem("Secondsofshow",val)
      } 
      else if(props.num==11)
      {
        localStorage.setItem("Retriesuntilspeech",val)
      } 
      else if(props.num==15)
      {
        localStorage.setItem("Retriesuntilvibrate",val)
      } 
      else if(props.num==16)
      {
        localStorage.setItem("Secondsuntilfirstprompt",val)
      } 
      props.setPromptVal(parseFloat(props.promptVal)+1);
    }
  }

  const promptDecrement =()=>{
    if(props.promptVal>0)
    {
      const val = parseFloat(props.promptVal)-1;
      if(props.num==1)
      {
        localStorage.setItem("Secondsuntilprompt",val)
        
      } 
      else if(props.num==2)
      {
        localStorage.setItem("Secondsuntilreprompt",val)
      } 
      else if(props.num==3)
      {
       localStorage.setItem("Numberofretries",val) 
      } 
      else if(props.num==5)
      {
        localStorage.setItem("Retriesuntilarrow",val)
      } 
      else if(props.num==6)
      {
        localStorage.setItem("Secondsofshow",val)
      } 
      else if(props.num==11)
      {
        localStorage.setItem("Retriesuntilspeech",val)
      } 
      else if(props.num==15)
      {
        localStorage.setItem("Retriesuntilvibrate",val)
      } 
      else if(props.num==16)
      {
        localStorage.setItem("Secondsuntilfirstprompt",val)
      } 
      props.setPromptVal(val);
    }
  }

  const onChange = (checked) => {
    console.log(`switch to ${checked}`);
    props.onChange(checked)
    setChecked(checked)
    if(props.num==4)
    {
      localStorage.setItem("Showpromptarrow",checked)
    } 
    else if(props.num==7)
    {
      localStorage.setItem("Usearrowanimation",checked)
      
    } 
    else if(props.num==9)
    {
      localStorage.setItem("Usepagetransitions",checked)
      
    } 
    else if(props.num==10)
    {
      localStorage.setItem("Playpromptaudio",checked)
      
    } 
    else if(props.num==14)
    {
      localStorage.setItem("usehapticfeedback",checked)
    } 

  };

  const onChange4 = ({ target: { value } }) => {
    console.log('radio4 checked', value);
    props.setValue(value);
    if(props.num==8)
    {
      localStorage.setItem("Arrowanimation",value)
      
    }
    else if(props.num==12)
    {
      localStorage.setItem("Promptvoice",value)
      
    }
    else if(props.num==13)
    {
      localStorage.setItem("Speechrate",value)
    }
  };

  return (
    <div className='prompt-Container'>
      <div className='prompt-Container-heading'>{props.heading}</div>
      {props.incrementPrompt==true && <div className='increment-container'>
          <div className='prompt-text'>{props.incrementValue}</div>
          <div className='increment-sub-container'>
            <div className='increment-icon' onClick={()=>promptDecrement()}>-</div> | <div className='increment-icon' onClick={()=>promptIncrement()}>+</div>
          </div>
      </div>
      }
      {props.switch===true && <div className='increment-container'>
        <Switch disabled={props.num==14 && localStorage.getItem("OS")=="IOS" ? true : false} checked={checked} style={{ backgroundColor: checked ? '#4cd964' : '#efeff4'}} onChange={onChange} />
      </div>
      }

      {props.radio===true && <div className='increment-container'>
      <Radio.Group buttonStyle="solid" size='small' optionType="button" options={props.data} onChange={onChange4} value={props.value} />
      </div>
      }
      </div>
  )
}
