import React,{useState} from 'react'

export default function SpeakSinglePrompt(props) {
    const [val,setVal] = useState(props.propmtVal)

    const getVal =(val)=>{
        console.log(val.target.value)
        props.setPromptVal(val.target.value)

        if(props.num==1)
        {
            localStorage.setItem("textOnPhoneBigger",val.target.value)
        }
        else if(props.num==2)
        {
            localStorage.setItem("tapSettingsBtn",val.target.value)
        }

        else if(props.num==3)
        {
            localStorage.setItem("swipeUpDisplayBrightness",val.target.value)
        }
        else if(props.num==4)
        {
            localStorage.setItem("tapDisplayBrightness",val.target.value)
        }

        else if(props.num==5)
        {
            localStorage.setItem("swipedownTextSize",val.target.value)
        }
        else if(props.num==6)
        {
            localStorage.setItem("tapTextSize",val.target.value)
        }
        else if(props.num==7)
        {
            localStorage.setItem("tapTheBarRight",val.target.value)
        }

        else if(props.num==8)
        {
            localStorage.setItem("taskTimeOut",val.target.value)
        }

        else if(props.num==9)
        {
            localStorage.setItem("welldone",val.target.value)
        }
        else if(props.num==10)
        {
            localStorage.setItem("tapback",val.target.value)
        }

        

    }
  return (
    <div className='speakSinglePrompt'>
        <div className='speakSinglePromptHead'>{props.propmtVal}</div>
        <input className='speakSingleInput' value={props.propmtVal} onChange={evt => getVal(evt)}/>
    </div>
  )
}
